.product {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.product__front,
.product__back {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  backface-visibility: hidden;
  transition: transform 1s ease;
}

.product__front {
  transform: perspective(600px) rotateY(0deg);
}

.product__front::before {
  content: attr(label);
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 44.68px;
  height: 20.42px;
  top: 0;
  left: 0;
  background: #ff6868;
  border-radius: 0px 0px 5px 0px;
  font-family: "Mulish", sans-serif;
  font-weight: 600;
  font-size: 12px;
  line-height: 130%;
  letter-spacing: 0.008em;
  text-transform: capitalize;
  color: #fff;
  z-index: 10;
}

.product__front[label=new]::before {
  background: #00b7f1;
}

.product__front[label=sale]::before {
  background: #7ac751;
}

.product__back {
  padding: 40px 60px 40px 20px;
  transform: perspective(600px) rotateY(180deg);
  background: #f5f5f5;
}

.product__img {
  padding-bottom: 15px;
  min-width: 100%;
  max-height: 288px;
  object-fit: cover;
}

.product__suptitle {
  font-family: "Mulish", sans-serif;
  font-weight: 400;
  line-height: 130%;
  letter-spacing: 0.008em;
  text-transform: uppercase;
  color: rgba(117, 117, 117, 0.71);
}

.product__title {
  padding: 9px 0px;
  font-family: "Rubik", sans-serif;
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 120%;
  letter-spacing: 0.005em;
  color: #555;
}

.product__stats {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.product__actions {
  position: absolute;
  top: 60px;
  right: -40px;
  display: flex;
  flex-direction: column;
  gap: 9px;
  opacity: 0;
  transition: all 0.3s ease;
  z-index: 10;
}

.product__action a {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  border-radius: 444px;
  width: 38px;
  height: 38px;
  transition: background 0.3s ease;
}

.product__action a svg {
  fill: #757575;
  transition: fill 0.3s ease;
}

.product__action a.pressed {
  background: #7ac751;
}

.product__action a.pressed svg {
  fill: #fff;
}

.product__rating {
  display: flex;
  gap: 3.58px;
}

.product__star {
  stroke: #ccc;
}

.product__star.active {
  stroke: #7ac751;
}

.product:hover .product__actions {
  right: 15px;
  opacity: 1;
}

.product.inverted .product__front {
  transform: perspective(600px) rotateY(-180deg);
}

.product.inverted .product__back {
  transform: perspective(600px) rotateY(0deg);
}

.product.disabled {
  opacity: 0.6;
}

.product.hidden {
  display: none;
}

.prices {
  display: flex;
  gap: 7px;
}

.prices__actual-price,
.prices__old-price {
  font-family: "Mulish", sans-serif;
  font-weight: 700;
  font-size: 1.125rem;
  line-height: 120%;
  letter-spacing: 0.005em;
  color: #7ac751;
}

.prices__old-price {
  font-weight: 600;
  color: #ccc;
}

.btn {
  padding: 10px 18px;
  font-family: "Mulish", sans-serif;
  font-weight: 500;
  font-size: 1rem;
  text-transform: uppercase;
  background: #7ac751;
  border-radius: 5px;
  color: #fff;
}

