.burger {
  display: none;
  position: relative;
  cursor: pointer;
  width: 30px;
  height: 20px;
  background: transparent;
}

.burger span {
  position: absolute;
  top: 9px;
  right: 0;
  width: 100%;
  height: 2px;
  background: #fff;
  transition: all 0.3s;
}

.burger::before,
.burger::after {
  content: "";
  position: absolute;
  right: 0;
  width: 100%;
  height: 2px;
  background: #fff;
  transition: all 0.3s;
}

.burger::before {
  top: 0;
}

.burger::after {
  bottom: 0;
}

.burger.active::before {
  transform: rotate(45deg);
  top: 9px;
}

.burger.active::after {
  transform: rotate(-45deg);
  bottom: 9px;
}

.burger.active span {
  transform: scale(0);
}

