// заголовки, БЭМ блоки etc.
.pointer {
   cursor: pointer;
}
.noselect {
   -webkit-touch-callout: none;
   -webkit-user-select: none;
   -khtml-user-select: none;
   -moz-user-select: none;
   -ms-user-select: none;
   user-select: none;
}
.select {
   &__btn {
      border: none;
      background: transparent;
   }
   &__list {
      color: $mainFontColor;
   }
   &__item:hover {
      background: $mainColor;
   }
}
.title {
   padding-bottom: 45px;
   font-family: "Mulish", sans-serif;
   font-weight: 700;
   font-size: rem(36);
   @include responsiveValue(font-size, 33.7, 28);
   text-transform: uppercase;
   text-align: center;
   color: $mainFontColor;
}
.tabs {
   max-width: 100%;
   box-shadow: none;
   border-radius: 0;
   .nav {
      @include responsiveValue(gap, 60, 15);
      padding: 0px 0px 73px 0px;
      justify-content: center;
      white-space: nowrap;
      flex-wrap: wrap;
      &::-webkit-scrollbar {
         width: 0;
         height: 0;
      }
      &__item {
         position: relative;
         font-family: "Mulish", sans-serif;
         font-weight: 500;
         font-size: rem(24);
         @include responsiveValue(font-size, 23, 20);
         color: #c2c2c2;
         transition: color 0.3s ease;
         &::before {
            content: none;
         }
         &:hover {
            color: $mainColor;
         }
         &.active {
            color: $mainColor;
         }
      }
   }
   &__content {
      padding: 0;
   }
   &__items {
      display: grid;
      column-gap: 40px;
      row-gap: 45px;
      grid-template-columns: repeat(auto-fit, minmax(270px, 1fr));
      grid-auto-rows: minmax(387px, auto);
   }
   &__btn {
      margin: 50px auto;
   }
}
.product {
   position: relative;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   &__front,
   &__back {
      position: absolute;
      width: 100%;
      height: 100%;
      overflow: hidden;
      backface-visibility: hidden;
      transition: transform 1s ease;
   }
   &__front {
      transform: perspective(600px) rotateY(0deg);
      &::before {
         content: attr(label);
         position: absolute;
         display: flex;
         justify-content: center;
         align-items: center;
         width: 44.68px;
         height: 20.42px;
         top: 0;
         left: 0;
         background: #ff6868;
         border-radius: 0px 0px 5px 0px;

         font-family: "Mulish", sans-serif;
         font-weight: 600;
         font-size: 12px;
         line-height: 130%;
         letter-spacing: 0.008em;
         text-transform: capitalize;
         color: #fff;

         z-index: 10;
      }
      &[label="new"]::before {
         background: #00b7f1;
      }
      &[label="sale"]::before {
         background: $mainColor;
      }
   }
   &__back {
      padding: 40px 60px 40px 20px;
      transform: perspective(600px) rotateY(180deg);
      background: #f5f5f5;
   }
   &__img {
      padding-bottom: 15px;
      min-width: 100%;
      max-height: 288px;
      object-fit: cover;
   }
   &__suptitle {
      font-family: "Mulish", sans-serif;
      font-weight: 400;
      line-height: 130%;
      letter-spacing: 0.008em;
      text-transform: uppercase;
      color: rgba(117, 117, 117, 0.71);
   }
   &__title {
      padding: 9px 0px;
      font-family: "Rubik", sans-serif;
      font-weight: 400;
      font-size: rem(20);
      line-height: 120%;
      letter-spacing: 0.005em;
      color: $mainFontColor;
   }
   &__stats {
      display: flex;
      justify-content: space-between;
      align-items: center;
   }
   &__actions {
      position: absolute;
      top: 60px;
      right: -40px;
      display: flex;
      flex-direction: column;
      gap: 9px;
      opacity: 0;
      transition: all 0.3s ease;
      z-index: 10;
   }
   &__action a {
      display: flex;
      justify-content: center;
      align-items: center;
      background: #fff;
      border-radius: 444px;
      width: 38px;
      height: 38px;
      transition: background 0.3s ease;
      svg {
         fill: #757575;
         transition: fill 0.3s ease;
      }
      &.pressed {
         background: $mainColor;
         svg {
            fill: #fff;
         }
      }
      @media (any-hover: hover) {
         &:hover {
            background: $mainColor;
            svg {
               fill: #fff;
            }
         }
      }
   }
   &__rating {
      display: flex;
      gap: 3.58px;
   }
   &__star {
      stroke: #ccc;
      &.active {
         stroke: $mainColor;
      }
   }
   &:hover .product__actions {
      right: 15px;
      opacity: 1;
   }
   &.inverted {
      .product__front {
         transform: perspective(600px) rotateY(-180deg);
      }
      .product__back {
         transform: perspective(600px) rotateY(0deg);
      }
   }
   &.disabled {
      opacity: 0.6;
   }
   &.hidden {
      display: none;
   }
}
.prices {
   display: flex;
   gap: 7px;
   &__actual-price,
   &__old-price {
      font-family: "Mulish", sans-serif;
      font-weight: 700;
      font-size: rem(18);
      line-height: 120%;
      letter-spacing: 0.005em;
      color: $mainColor;
   }
   &__old-price {
      font-weight: 600;
      color: #ccc;
   }
}
.btn {
   padding: 10px 18px;

   font-family: "Mulish", sans-serif;
   font-weight: 500;
   font-size: rem(16);
   text-transform: uppercase;
   background: $mainColor;
   border-radius: 5px;
   color: #fff;
}
