.hero {
   .menu {
      background: #f7f8fa;
      white-space: nowrap;
      &__container {
         display: flex;
         align-items: center;
         @include responsiveValue(gap, 90, 10);
      }
      &__list {
         display: flex;
         @include responsiveValue(gap, 65, 10);
         overflow-x: scroll;
         -ms-overflow-style: none;
         scrollbar-width: none;
         &::-webkit-scrollbar {
            width: 0;
            height: 0;
         }
      }
      &__item {
         text-transform: uppercase;
      }
      &__link {
         color: $mainFontColor;
         transition: color 0.3s ease;
         @media (any-hover: hover) {
            &:hover {
               color: $mainColor;
            }
         }
      }
      .sidebar {
         position: relative;
         width: 260px;
         &__title {
            cursor: pointer;
            display: flex;
            justify-content: center;
            gap: 20px;
            padding: 22px 0px;
            background: $mainColor;
            font-size: rem(16);
            font-weight: 600;
            letter-spacing: 0.02em;
            text-transform: uppercase;
            color: #fff;
         }
         &__burger {
            position: relative;
            width: 24px;
            height: 16px;
            background: transparent;
            span {
               position: absolute;
               top: 50%;
               transform: translateY(-50%);
               left: 0;
               width: 12px;
               height: 2px;
               background: #fff;
               transition: all 0.3s;
            }
            &::before,
            &::after {
               content: "";
               position: absolute;
               left: 0;
               height: 2px;
               background: #fff;
               transition: all 0.3s;
            }
            &::before {
               top: 0;
               width: 18px;
            }
            &::after {
               bottom: 0;
               width: 6px;
            }
         }
         &__list {
            position: absolute;
            display: none;
            width: 260px;
            max-height: 455px;
            background: #f7f8fa;
            z-index: 10;
            &.active {
               display: block;
            }
         }
         &__item {
            position: relative;
            letter-spacing: 0.02em;
            line-height: 17px;
            &:not(:last-child)::after {
               content: "";
               position: absolute;
               left: 0;
               bottom: 0;
               width: 100%;
               height: 0.8px;
               background: #e3e3e3;
            }
         }
         &__link {
            display: inline-block;
            padding: 10px 0px 14px 30px;
            width: 100%;
            color: $mainFontColor;
            transition: color 0.3s ease;
            &:hover {
               color: $mainColor;
            }
         }
      }
   }
   .slider {
      max-width: 899px;
      height: 100%;
      margin-left: auto;
      margin-top: 12px;
      &.fullscreen {
         max-width: 100%;
      }
      &__item {
         display: flex !important;
         justify-content: space-between;
         align-items: center;
         padding: 36px 40px 29px 48px;
         @include responsiveValue(padding-left, 48, 15);
         @include responsiveValue(padding-right, 40, 15);
         background: linear-gradient(103.4deg, rgba(241, 250, 255, 0.88) 0.89%, #f1faff 101.61%);
         backdrop-filter: blur(155px);
         border-radius: 1px;
      }
      &__disc {
         max-width: 318px;
      }
      &__suptitle {
         @include responsiveValue(font-size, 20, 16);
         text-transform: uppercase;
         letter-spacing: 0.05em;
         color: $mainColor;
      }
      &__title {
         position: relative;
         padding: 5px 0px 27px 0px;
         @include responsiveValue(font-size, 36.3, 26);
         font-weight: 600;
         line-height: 47px;
         line-height: 117%;
         color: #2f2f2f;
         &::after {
            content: url("../img/decoration_1.webp");
            position: absolute;
            bottom: 15px;
            right: 0;
         }
      }
      &__subtitle {
         padding-bottom: 35px;

         @include responsiveValue(font-size, 26, 22);
         font-family: "Mulish", sans-serif;
         font-weight: 600;
         color: rgba(81, 81, 81, 0.71);
      }
      &__img img {
         max-width: 100%;
      }
      .products {
         display: flex;
         flex-direction: column;
         gap: 25px;
         &__item {
            display: flex;
            flex-direction: column;
            align-items: center;
            padding: 5px 25px;
            width: 108px;
            height: 101px;
            white-space: nowrap;
            background: linear-gradient(180deg, rgba(255, 255, 255, 0.6) 0%, rgba(255, 255, 255, 0.56) 100%);
            border: 2px solid #ffffff;
            border-radius: 8px;
         }
         &__price {
            font-size: rem(11);
            font-weight: 600;
            color: $mainColor;
            line-height: 26px;
         }
         &__text {
            font-size: rem(10);
            font-weight: 600;
         }
         &__img {
            max-width: 50px;
         }
      }
      .slick-dots {
         position: absolute;
         transform: translateX(-50%);
         left: 50%;
         bottom: 32px;
         display: flex;
         align-items: center;
         gap: 15px;
         li {
            width: 12px;
            height: 12px;
            border-radius: 50%;
            background: rgba(122, 199, 81, 0.29);
            transition: all 0.3s ease;
         }
         .slick-active {
            width: 24px;
            height: 9px;
            border-radius: 666px;
            background: #7ac751;
         }
         button {
            color: transparent;
         }
      }
   }
   .advantage-cards {
      &__container {
         display: flex;
         flex-wrap: wrap;
         padding: 65px 15px;
      }
      &__item {
         display: flex;
         justify-content: center;
         align-items: center;
         flex: 1 1 25%;
         gap: 15px;
      }
      &__title {
         font-weight: 600;
         font-size: rem(20);
         line-height: 130%;
      }
      &__subtitle {
         line-height: 186%;
         color: rgba(81, 81, 81, 0.71);
      }
   }
}

@media (max-width: $mobile) {
   .hero {
      .sidebar {
         width: 62px !important;
         &__burger + span {
            display: none;
         }
         &__title {
            width: 60px;
         }
         &__list {
            height: 250px;
            overflow-y: scroll;
         }
      }
      .slider {
         &__item {
            flex-direction: column-reverse;
         }
         &__disc {
            order: 1;
         }
         &__img {
            order: 2;
         }
         &__btn {
            width: 100%;
         }
         &__title {
            &::after {
               left: 0;
            }
         }
         .products {
            display: none;
         }
         .slick-dots {
            top: 20px;
            bottom: auto;
         }
      }
   }
}
@media (max-width: em(1200)) {
   .hero {
      .slider {
         max-width: 100%;
      }
      .advantage-cards {
         &__item {
            flex-basis: 50%;
            margin-bottom: 40px;
         }
      }
   }
}
@media (max-width: $mobileSm) {
   .hero {
      .advantage-cards {
         &__item {
            flex-basis: 100%;
         }
      }
   }
}
