.footer {
   &__container {
      display: flex;
      gap: 83px;
      padding: 115px 15px 50px 15px;
   }
   &__item {
      &:first-child {
         max-width: 300px;
         flex: 0 1 300px;
      }
      &:last-child {
         display: flex;
         justify-content: space-between;
         flex: auto;
         gap: 40px;
         flex-wrap: wrap;
         white-space: nowrap;
      }
   }
   &__text {
      padding: 15px 0px;
      font-size: rem(16);
      line-height: 200%;
   }
   &__icons {
      display: flex;
      align-items: center;
      gap: 35px;
      svg {
         fill: $mainFontColor;
         transition: fill 0.3s ease;
         &:hover {
            fill: $mainColor;
         }
      }
   }
   .list {
      display: flex;
      flex-direction: column;
      gap: 15px;
      font-size: rem(18);
      letter-spacing: 0.01em;
      &__title {
         font-weight: 600;
      }
      &__link {
         color: $mainFontColor;
         transition: color 0.3s ease;
         &:hover {
            color: $mainColor;
         }
      }
   }
   .copy {
      position: relative;
      &__container {
         display: flex;
         justify-content: space-between;
         padding: 33px 15px;
      }
      &__links {
         display: flex;
         gap: 50px;
      }
      &::before {
         content: "";
         position: absolute;
         top: 0;
         left: 0;
         width: 100%;
         height: 1px;
         background: rgba(150, 150, 150, 0.2);
      }
   }
}

@media (max-width: 520px) {
   .footer {
      &__container {
         flex-direction: column;
         gap: 0;
      }
      .copy {
         &__container {
            flex-direction: column;
            text-align: center;
            gap: 20px;
         }
         &__links {
            justify-content: center;
         }
      }
   }
}
