.reviews {
   &__container {
      padding: 0px 15px 115px 15px;
   }
   .slider {
      max-width: 816px;
      margin: 0px auto;
      &__item {
         position: relative;
         padding: 47px 25px;
         max-width: 100%;
         display: flex !important;
         flex-direction: column;
         justify-content: center;
         align-items: center;
         text-align: center;
         gap: 32px;
         background: #f8faf8;
      }
      &__text {
         max-width: 566px;
         font-family: "DM Sans", sans-serif;
         font-size: rem(16);
         line-height: 150%;
         text-align: center;
         letter-spacing: 0.005em;
         color: #515151;
      }
      &__name {
         padding-bottom: 8px;
         font-family: "Mulish", sans-serif;
         font-weight: 600;
         font-size: rem(24);
         letter-spacing: 0.005em;
         color: $mainFontColor;
      }
      &__status {
         font-family: "Rubik", sans-serif;
         font-size: rem(16);
         letter-spacing: 0.005em;
         color: #515151;
      }
      picture {
         display: flex;
         justify-content: center;
      }
      .slick-arrow {
         color: transparent;
         &::before {
            content: "";
            -webkit-mask: url('data:image/svg+xml; utf8, <svg xmlns="http://www.w3.org/2000/svg" width="24" height="38" viewBox="0 0 24 38"><path fill-rule="evenodd" clip-rule="evenodd" d="M23.092 34.2413C23.4822 33.8511 23.4826 33.2185 23.0928 32.8279L9.9998 19.7063C9.61031 19.316 9.61031 18.684 9.9998 18.2937L23.0928 5.17211C23.4826 4.78146 23.4822 4.14889 23.092 3.75866L20.0404 0.707106C19.6499 0.316582 19.0168 0.316582 18.6262 0.707107L1.04045 18.2929C0.649921 18.6834 0.649921 19.3166 1.04045 19.7071L18.6262 37.2929C19.0168 37.6834 19.6499 37.6834 20.0404 37.2929L23.092 34.2413Z"/></svg>');
            mask: url('data:image/svg+xml; utf8, <svg xmlns="http://www.w3.org/2000/svg" width="24" height="38" viewBox="0 0 24 38"><path fill-rule="evenodd" clip-rule="evenodd" d="M23.092 34.2413C23.4822 33.8511 23.4826 33.2185 23.0928 32.8279L9.9998 19.7063C9.61031 19.316 9.61031 18.684 9.9998 18.2937L23.0928 5.17211C23.4826 4.78146 23.4822 4.14889 23.092 3.75866L20.0404 0.707106C19.6499 0.316582 19.0168 0.316582 18.6262 0.707107L1.04045 18.2929C0.649921 18.6834 0.649921 19.3166 1.04045 19.7071L18.6262 37.2929C19.0168 37.6834 19.6499 37.6834 20.0404 37.2929L23.092 34.2413Z"/></svg>');
            -webkit-mask-size: cover;
            mask-size: cover;
            background-color: #ccc;
            transition: background-color 0.3s ease;
            position: absolute;
            width: 25px;
            height: 38px;
            top: 50%;
            transform: translateY(-50%);
            left: -56px;
            z-index: 10;
         }
         &:hover::before {
            background-color: $mainColor;
         }
      }
      .slick-next::before {
         top: 46%;
         left: auto;
         right: -56px;
         transform: rotate(180deg);
      }
   }
}

@media (max-width: $tablet) {
   .reviews .slider {
      .slick-arrow::before {
         top: auto;
         bottom: 4%;
         left: 10%;
      }
      .slick-next::before {
         left: auto;
         right: 10%;
         bottom: 7%;
      }
   }
}
