.header {
   .top-header {
      font-size: rem(16);
      background: $mainColor;
      color: #fff;
      &__container {
         display: flex;
         align-items: center;
         padding: 15px;
      }
      &__text {
         flex: auto;
      }
      &__controls {
         display: flex;
         align-items: center;
         gap: 30px;
      }
      .select {
         position: relative;
         &__wrapper {
            width: 141px;
         }
         &__btn {
            display: flex;
            justify-content: space-between;
            padding: 8px 10px;
            color: #fff;
         }
         &__list {
            white-space: nowrap;
            background: #f7f8fa;
         }
         &::after {
            content: "";
            position: absolute;
            top: 0;
            right: -10px;
            width: 2px;
            height: 100%;
            background: #fff;
         }
      }
   }
   .main-header {
      &__container {
         display: flex;
         justify-content: space-between;
         align-items: center;
         gap: 20px;
         padding: 21px 15px 25px 15px;
      }
      .search-form {
         flex: auto;
         display: flex;
         max-width: 582px;
         height: 46px;
         border: 2px $mainColor solid;
         border-radius: 8px;
         &__input {
            width: 100%;
            height: 100%;
            padding: 0px 25px;
            font-size: rem(17);
            border-radius: 8px;
            background: #fff;
            &::placeholder {
               font-size: rem(14);
               color: #b0b0b0;
            }
            &::-webkit-search-cancel-button {
               position: relative;
               cursor: pointer;
               -webkit-appearance: none;
               height: 10px;
               right: -12px;
               width: 10px;
               background: url('data:image/svg+xml,\ <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" version="1.1" id="Capa_1" x="0px" y="0px" width="10.926px" height="10.926px" viewBox="0 0 94.926 94.926" style="enable-background:new 0 0 94.926 94.926;" xml:space="preserve"><g><path d="M55.931,47.463L94.306,9.09c0.826-0.827,0.826-2.167,0-2.994L88.833,0.62C88.436,0.224,87.896,0,87.335,0   c-0.562,0-1.101,0.224-1.498,0.62L47.463,38.994L9.089,0.62c-0.795-0.795-2.202-0.794-2.995,0L0.622,6.096   c-0.827,0.827-0.827,2.167,0,2.994l38.374,38.373L0.622,85.836c-0.827,0.827-0.827,2.167,0,2.994l5.473,5.476   c0.397,0.396,0.936,0.62,1.498,0.62s1.1-0.224,1.497-0.62l38.374-38.374l38.374,38.374c0.397,0.396,0.937,0.62,1.498,0.62   s1.101-0.224,1.498-0.62l5.473-5.476c0.826-0.827,0.826-2.167,0-2.994L55.931,47.463z"/></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g>\</svg>')
                  no-repeat;
            }
         }
         .select {
            position: relative;
            &__wrapper {
               width: 123px;
               height: 100%;
            }
            &__btn {
               height: 100%;
               gap: 13px;
               color: $mainFontColor;
            }
            &__list {
               background: #f7f8fa;
            }
            &::before {
               content: "";
               position: absolute;
               top: 0;
               width: 1.5px;
               height: 100%;
               background: rgba(227, 227, 227, 0.78);
            }
         }
         &__btn {
            background: $mainColor;
            padding: 0px 20px;
         }
      }
      &__actions {
         display: flex;
         gap: 25px;
         *:first-child {
            position: relative;
            &::before {
               content: attr(data-count);
               position: absolute;
               top: -3px;
               right: -8px;
               min-width: 18px;
               min-height: 18px;
               padding: 3px;
               display: flex;
               justify-content: center;
               align-items: center;
               font-size: 12px;
               color: #fff;
               border-radius: 400px;
               background: $mainColor;
               z-index: 1;
            }
         }
      }
   }
}
// burger
.burger {
   display: none;
   position: relative;
   cursor: pointer;
   width: 30px;
   height: 20px;
   background: transparent;
   span {
      position: absolute;
      top: 9px;
      right: 0;
      width: 100%;
      height: 2px;
      background: #fff;
      transition: all 0.3s;
   }
   &::before,
   &::after {
      content: "";
      position: absolute;
      right: 0;
      width: 100%;
      height: 2px;
      background: #fff;
      transition: all 0.3s;
   }
   &::before {
      top: 0;
   }
   &::after {
      bottom: 0;
   }
   &.active {
      &::before {
         transform: rotate(45deg);
         top: 9px;
      }
      &::after {
         transform: rotate(-45deg);
         bottom: 9px;
      }
      span {
         transform: scale(0);
      }
   }
}
@media (max-width: $mobile) {
   .header {
      .main-header {
         &__container {
            flex-wrap: wrap;
         }
         .search-form {
            flex: auto;
            order: 3;
            max-width: 100%;
         }
         &__actions {
            order: 2;
         }
      }
   }
}
@media (max-width: $mobileXs) {
   .header {
      .main-header {
         .search-form {
            .select {
               &__wrapper {
                  width: 39px;
               }
               &__list {
                  width: 95px;
               }
               &__btn span {
                  display: none;
               }
            }
         }
      }
   }
}
@media (max-width: $mobileSm) {
   .header {
      .top-header {
         &__container {
            justify-content: center;
         }
         &__text {
            display: none;
         }
      }
   }
}
@media (max-width: $mobile) {
   .burger {
      display: block;
   }
   .header {
      .menu {
         overflow-y: scroll;
         position: absolute;
         top: 0;
         right: -100%;
         flex-direction: column;
         gap: 20px;
         height: 100vh;
         width: 50%;
         padding: 100px 20px 30px 20px;
         background: #666;
         box-shadow: -4px 0px 13px 0px rgba(34, 60, 80, 0.2);
         transition: right 0.6s;
         &.active {
            right: 0;
         }
         &__list {
            flex-direction: column;
            text-align: center;
         }
         &__btn {
            width: 100%;
         }
      }
   }
}
@media (max-width: $mobileSm) {
   .header {
      .menu {
         width: 70%;
      }
   }
}
@media (max-width: $mobileXs) {
   .header {
      .menu {
         width: 80%;
      }
   }
}
