.articles {
   &__title {
      padding-bottom: 60px;
   }
   &__items {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(385px, 1fr));
      grid-auto-rows: minmax(455px, auto);
      column-gap: 26px;
      row-gap: 22px;
   }
   .article {
      cursor: pointer;
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: end;
      padding: 30px 0px;
      @include responsiveValue(padding-left, 38, 16);
      @include responsiveValue(padding-right, 38, 16);
      border-radius: 8px;
      z-index: 2;
      background: url("../img/img_21.webp") center/cover no-repeat;
      transition: all 0.4s ease;
      &:nth-child(2) {
         background: url("../img/img_22.webp") center/cover no-repeat;
      }
      &:nth-child(3) {
         background: url("../img/img_23.webp") center/cover no-repeat;
      }
      &:last-child {
         background: url("../img/img_24.webp") center/cover no-repeat;
      }
      @media (any-hover: hover) {
         &:hover {
            transform: translateY(-10px);
            box-shadow: 0px 0px 35px -10px rgba(34, 60, 80, 0.2);
            .article__link {
               transform: translateX(10px);
            }
         }
      }
      * {
         z-index: 2;
      }
      &__suptitle {
         padding-bottom: 20px;
         display: flex;
         align-items: center;
         gap: 5px;
      }
      &__label {
         position: relative;
         padding: 6px 10px;
         display: flex;
         gap: 10px;
         font-family: "DM Sans", sans-serif;
         font-weight: 500;
         font-size: rem(12);
         letter-spacing: 0.008em;
         text-transform: uppercase;
         color: $mainColor;
         border-radius: 3px;
         background: #f8faf8;
