            -webkit-mask-size: cover;
            mask-size: cover;
            width: 13px;
            height: 13px;
            background-color: $mainColor;
            transition: background-color 0.3s ease;
         }
      }
      &__date {
         font-family: "Mulish", sans-serif;
         font-size: rem(14);
         line-height: 130%;
         letter-spacing: 0.008em;
         color: #fff;
      }
      &__title {
         @include responsiveValue(padding-bottom, 31, 15);
         @include responsiveValue(font-size, 36, 28);
         line-height: 120%;
         letter-spacing: 0.005em;
         text-transform: none;
         text-align: left;
         color: #fff;
      }
      &__link {
         font-family: "DM Sans", sans-serif;
         font-weight: 500;
         font-size: rem(14);
         line-height: 130%;
         letter-spacing: 0.008em;
         color: #fff;
         transition: transform 0.3s ease;
      }
      &::before {
         content: "";
         position: absolute;
         top: 0;
         left: 0;
         width: 100%;
         height: 100%;
         border-radius: 8px;
         background-color: rgba(0, 0, 0, 0.5);
         z-index: 1;
      }
   }
}

@media (max-width: $mobile) {
   .articles {
      &__items {
         grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
      }
   }
}
