.offer {
   &__row {
      display: flex;
      gap: 17px;
   }
   &__column {
      display: flex;
      flex-basis: 40%;
      flex-direction: column;
      gap: 17px;
      .offer__item {
         flex-basis: 50%;
         &:first-child {
            background: url(../img/img_2.webp) center/cover no-repeat;
         }
         &:last-child {
            background: url(../img/img_1.webp) center/cover no-repeat;
         }
      }
      .offer__title {
         font-family: "Mulish", sans-serif;
         font-weight: 600;
         padding-bottom: 8px;
      }
      .offer__subtitle {
         padding-bottom: 8px;
      }
   }
   &__item {
      flex-basis: 57%;
      height: 511px;
      background: url(../img/img_3.webp) center/cover no-repeat;
      padding: 50px 30px;
      transition: all 0.4s ease;
      @media (any-hover: hover) {
         &:hover {
            transform: translateY(-10px);
            box-shadow: 0px 0px 35px -10px rgba(34, 60, 80, 0.2);
            .offer__link {
               &::before {
                  bottom: -5px;
                  opacity: 1;
               }
               &::after {
                  right: -20px;
               }
            }
         }
      }
   }
   &__title {
      font-family: "Mulish", sans-serif;
      font-weight: 700;
      font-size: rem(22);
      line-height: 118%;
      color: $mainFontColor;
      padding-bottom: 10px;
   }
   &__subtitle {
      font-family: "Mulish", sans-serif;
      font-weight: 600;
      font-size: rem(20);
      line-height: 130%;
      color: $mainColor;
      padding-bottom: 12px;
      span {
         font-weight: 800;
      }
   }
   &__link {
      position: relative;
      font-family: "Mulish", sans-serif;
      font-weight: 400;
      font-size: rem(16);
      line-height: 130%;
      color: #757575;
      &::before {
         content: "";
         position: absolute;
         left: 0;
         bottom: -20px;
         width: 100%;
         height: 2px;
         opacity: 0;
         background: $mainColor;
         border-radius: 100%;
         transition: all 0.4s ease;
      }
      &::after {
         content: url('data:image/svg+xml; utf8, <svg xmlns="http://www.w3.org/2000/svg" width="12" height="9" viewBox="0 0 12 9"><path d="M11.3879 2.98231L8.87805 0.209356C8.75672 0.0753053 8.59217 0 8.4206 0C8.24903 0 8.08448 0.0753053 7.96314 0.209356C7.84184 0.343447 7.7737 0.52529 7.7737 0.714895C7.7737 0.9045 7.84184 1.08634 7.96314 1.22043L10.2634 3.76386H1.60212C1.43051 3.76386 1.26593 3.83919 1.14459 3.97329C1.02325 4.10739 0.955078 4.28926 0.955078 4.47891C0.955078 4.66855 1.02325 4.85042 1.14459 4.98452C1.26593 5.11862 1.43051 5.19395 1.60212 5.19395H10.3028L7.96314 7.77885C7.90298 7.84526 7.85526 7.92412 7.8227 8.01093C7.79014 8.09773 7.77338 8.19078 7.77338 8.28475C7.77338 8.37872 7.79014 8.47176 7.8227 8.55857C7.85526 8.64537 7.90298 8.72424 7.96314 8.79064C8.08448 8.9247 8.24903 9 8.4206 9C8.59217 9 8.75672 8.9247 8.87805 8.79064L11.3886 6.01697C11.7515 5.61381 11.9552 5.0682 11.9551 4.49949C11.955 3.93079 11.751 3.38529 11.3879 2.98231Z"/></svg>');
         position: absolute;
         right: -15px;
         opacity: 0.5;
         transition: right 0.3s ease;
      }
   }
}
@media (max-width: $mobile) {
   .offer {
      &__row {
         flex-direction: column;
      }
      &__column {
         flex-basis: 100%;
      }
   }
}
