html,
body,
.wrapper {
   height: 100%;
}
body {
   font-family: $fontFamily;
   font-size: $fontSize;
   font-weight: $regular;
   color: $mainFontColor;
}
body.blocked::before {
   content: "";
   position: absolute;
   width: 100%;
   height: 100%;
   background: rgba(0, 0, 0, 0.5);
   overflow: hidden;
   z-index: 20;
}
.wrapper {
   position: relative;
   overflow-x: hidden;
   display: flex;
   flex-direction: column;
   &.scroll-block {
      overflow-y: hidden;
   }
}
main {
   flex: auto;
}
