.special-offer {
   &__items {
      display: flex;
      gap: 30px;
   }
   .promotion {
      flex-basis: 50%;
      padding: 28px 24px;
      background: url("../img/img_12.webp") center/cover no-repeat;
      &:last-child {
         background: url("../img/img_13.webp") center/cover no-repeat;
      }
      &__text {
         max-width: 237px;
      }
      &__title {
         padding-bottom: 20px;

         font-family: "Mulish", sans-serif;
         font-weight: 600;
         font-size: rem(22);
         color: #555555;
      }
      &__subtitle {
         padding-bottom: 20px;

         font-family: "Mulish", sans-serif;
         font-size: rem(12);
         line-height: 150%;
         color: #757575;
      }
      &__order {
         padding-bottom: 30px;
         display: flex;
         align-items: center;
         gap: 19px;
      }
      &__btn {
         padding: 7px 10px;
      }
      &__discount-end {
         display: flex;
         gap: 18px;
      }
      &__circle {
         padding: 0px 5px;
         display: flex;
         flex-direction: column;
         justify-content: center;
         align-items: center;
         min-width: 40px;
         min-height: 40px;
         background: $mainColor;
         border-radius: 400px;
      }
      &__time-text,
      &__time-count {
         font-family: "Rubik", sans-serif;
         font-size: 8px;
         line-height: 120%;
         letter-spacing: 0.005em;
         text-transform: uppercase;
         color: #fff;
      }
      &__time-count {
         font-size: rem(14);
         font-weight: 500;
      }
   }
}
@media (max-width: $mobile) {
   .special-offer {
      &__items {
         flex-direction: column;
      }
   }
}
