.signup,
.login,
.notifications,
.password-recovery,
.cart {
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   align-items: center;
   gap: 20px;
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   padding: 20px 30px;
   width: 400px;
   height: 580px;
   background: #f7f8fa;
   border-radius: 15px;
   box-shadow: 0px 0px 35px -10px rgba(34, 60, 80, 0.2);
   z-index: 30;
   visibility: hidden;
   opacity: 0;
   transition: all 0.3s ease;
   &__close-btn {
      position: absolute;
      top: 30px;
      right: 30px;
      width: 20px;
      height: 20px;
      &:hover div {
         background: $mainColor;
      }
      div {
         position: absolute;
         top: 50%;
         transform: translateY(-50%);
         left: 0;
         width: 100%;
         height: 2px;
         transform: rotate(45deg);
         background: #000;
         transition: background 0.3s ease;
         &:last-child {
            transform: rotate(-45deg);
         }
      }
   }
   &-form {
      width: 100%;
      &__fields {
         display: flex;
         flex-direction: column;
         gap: 20px;
         padding-bottom: 20px;
      }
      &__btns {
         display: flex;
         flex-direction: column;
         gap: 10px;
      }
      .checkboxes {
         display: flex;
         flex-direction: column;
         gap: 15px;
         padding-bottom: 50px;
         &__items {
            display: flex;
            flex-direction: column;
            gap: 5px;
         }
         &__item {
            display: flex;
            gap: 5px;
            label {
               display: flex;
               justify-content: center;
               align-items: center;
               gap: 5px;
               font-size: rem(13);
               &::before {
                  content: "";
                  width: 15px;
                  height: 15px;
                  border: 1px solid #adb5bd;
                  border-radius: 3px;
               }
            }
            input {
               position: absolute;
               z-index: -1;
               width: 1px;
               height: 1px;
               opacity: 0;
               &[checked] + label::before,
               &:checked + label::before {
                  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="10" height="9" viewBox="0 0 10 9" fill="none"><path d="M1 2.56522L4.07692 7L9 1" stroke="white" stroke-width="2"/></svg>')
                     $mainColor;
                  background-position: center;
                  background-repeat: no-repeat;
               }
            }
            &:last-child {
               color: $mainColor;
            }
         }
         &__text {
            font-size: rem(15);
         }
      }
      .field {
         position: relative;
         &__input {
            width: 100%;
            height: 38px;
            padding: 0px 10px;
            border: 1px #bcbcbc solid;
            border-radius: 5px;
            background: #f7f8fa;
            font-size: rem(16);
            font-family: "Rubik", sans-serif;
            caret-color: $mainColor;
            transition: all 0.3s ease;
            &:focus {
               border: 1px $mainColor solid;
            }
            &:focus ~ label,
            &:valid ~ label {
               top: 0;
               font-size: rem(12);
            }
         }
         &__label {
            pointer-events: none;
            position: absolute;
            top: 50%;
            left: 10px;
            transform: translateY(-50%);
            padding: 0px 3px;
            background: #f7f8fa;
            color: #bcbcbc;
            transition: all 0.3s ease;
         }
      }
      &__btn {
         width: 100%;
      }
      &__login-btn {
         background: transparent;
         color: $mainFontColor;
         border: 2px $mainColor solid;
      }
   }
   &.active {
      visibility: visible;
      opacity: 1;
   }
}
.password-recovery {
   height: 200px;
}
.login {
   height: 350px;
   &-form {
      &__options {
         display: flex;
         justify-content: space-between;
         align-items: center;
         padding-bottom: 50px;
      }
      &__checkboxes {
         padding: 0 !important;
      }
      &__password-recovery {
         height: 100%;
         color: rgba(0, 0, 0, 0.7);
         font-size: rem(12);
         &:hover {
            text-decoration: underline;
         }
      }
      &__login-btn {
         background: $mainColor;
         color: #fff;
      }
   }
}
.notifications__text {
   pointer-events: none;
   white-space: nowrap;
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   font-size: rem(24);
   font-weight: 700;
   color: rgba(0, 0, 0, 0.3);
}
.cart {
   flex-direction: row;
   align-items: inherit;
   padding: 40px 60px;
   width: 66%;
   height: 600px;
   &__body {
      display: flex;
      flex-direction: column;
      flex: auto;
   }
   &__main {
      position: relative;
      flex: auto;
      min-height: 200px;
   }
   &__text {
      position: absolute;
      top: 50%;
      left: 50%;
      white-space: nowrap;
      transform: translate(-50%, -50%);
      font-size: rem(24);
      font-weight: 700;
      color: rgba(0, 0, 0, 0.3);
   }
   .liked {
      &__link {
         color: $mainColor;
         &:hover {
            text-decoration: underline;
         }
      }
   }
   .title {
      text-align: start;
      padding-bottom: 10px;
   }
   .info {
      width: 30%;
      &__title {
         text-transform: none;
         font-size: rem(24);
         padding-bottom: 20px;
      }
      &__tally {
         display: flex;
         flex-direction: column;
         padding-bottom: 20px;
      }
      &__prices {
         padding-bottom: 50px;
      }
      &__price {
         display: flex;
         justify-content: space-between;
         gap: 15px;
         line-height: 30px;
         span {
            font-size: rem(18);
         }
      }
      .total {
         position: relative;
         padding: 20px 0px;
         &::before,
         &::after {
            content: "";
            position: absolute;
            top: 0;
            width: 100%;
            height: 1px;
            background: rgba(0, 0, 0, 0.2);
         }
         &::after {
            top: auto;
            bottom: 0;
         }
      }
      &__btn {
         width: 100%;
      }
   }
}

@media (max-width: em(1200)) {
   .cart {
      overflow-y: scroll;
      width: 100%;
      height: 100%;
      border-radius: 0;
   }
}
@media (max-width: $mobile) {
   .cart {
      flex-direction: column;
      gap: 120px;
      &__body {
         gap: 100px;
      }
      .info {
         width: 100%;
      }
   }
}
@media (max-width: $mobileSm) {
   .signup,
   .login,
   .notifications,
   .password-recovery {
      overflow-y: scroll;
      width: 100%;
      height: 100%;
      border-radius: 0;
      justify-content: start;
      gap: 10vh;
   }
}
