.select {
   &__wrapper {
      position: relative;
      width: 160px;
   }
   &__btn {
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      width: 100%;
      font-size: rem(15);
      color: #000;
      background: #fff;
      border: 1px #000 solid;
      border-radius: 5px;
   }
   &__list {
      overflow-x: hidden;
      position: absolute;
      top: 45px;
      max-height: 0;
      width: 100%;
      background: #fff;
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.3);
      border-radius: 5px;
      z-index: 1;
      transition: all 0.3s ease;
      -ms-overflow-style: none;
      scrollbar-width: none;
      &.active {
         max-height: 185px;
      }
      &::-webkit-scrollbar {
         width: 0;
         height: 0;
      }
   }
   &__item {
      cursor: pointer;
      position: relative;
      padding: 10px 15px;
      transition: all 0.3s ease;
      &:hover {
         background: #888;
         color: #fff;
      }
   }
   &__value {
      display: none;
   }
}
